body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* FLEX */
.flex { display: flex; }
.horizontal, .horizontal-only { flex-direction: row; }
.vertical, .vertical-horizontal { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
  .vertical-horizontal { flex-direction: row; }
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }

.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-start {
  justify-content: flex-start;
  align-items: center;
}

.width-80 {
  width: 80%;
}

/* TEXT CENTER */
.text-center { text-align: center; }
.text-left { text-align: left; }
.text-right { text-align: right; }

.container, .container-big {
  width: calc(100%-30px);
  max-width: 1024px;
  padding: 0 15px;
  margin: 0 auto;
}
.container-big {
  max-width: 1300px;
}

/* INPUT */
.form-input {
  padding: 8px 15px;
  margin: 5px;
  border: 1px solid #999;
  border-radius: 10px;
  font-size: 1rem;
}

/* FONT */
.bold {
  font-weight: bold;
}
.font-400 {
  font-weight: 400;
}

/* CONTAINER */
.NavBar {
  height: 90px;
  padding: 15px;
}
.NavBar > div {
  height: 100%;
}

.logo {
  width: 120px;
}

.Categorias {
  width: calc(100%-20px);
  /* background-color: #333; */
  background-color: #212640;
  padding: 0 10px;
  color: #fefefe;
}
.Categorias > div > div {
  padding: 5px;
  border-right: 1px solid #777;
  /* border-radius: 5px; */
  cursor: pointer;
}
.Categorias > div > div:last-child {
  border-right: 0px;
}
.Categorias > div > div:hover {
  background-color: #777;
}
@media screen and (max-width: 1000px){
  .Categorias > div {    
      overflow-x: scroll;
  }   
}
@media screen and (max-width: 800px){
  .NavBar {
    height: 150px;
  }
  .logo {
    width: 80px;
  }
}

.RedesSociais {
  background-color: #222840;
  padding: 40px 0 60px 0;
  color: #fefefe;
}
.RedesSociais > div > h2 {
  padding: 20px 0;
  color: #ff2020;
}

.Contato > div > div {
  padding: 8px;
}

.price > span {
  font-size: 1.2rem;
  font-weight: 300;
}

.button-cta {
  border-radius: 10px;
  border: 1px solid rgb(74, 0, 0);
  background-color: #c60000;
  padding: 10px 20px;
  width: 80%;
  font-size: 1.8rem;
  color: #fefefe;
  cursor: pointer;
}
.button-cta:hover {
  background-color: #610000;
}

.button-sm {
  padding: 5px 8px;
  font-size: 1.2rem;
  width: 50px;
}

.pics > div {
  border: 1px solid #777;
  margin: 20px;
  cursor: pointer;
}

.icons > div > i {
  padding: 5px;
  margin: 0 5px;
}

.Footer {
  background-color: #222840;
  padding: 40px 0 60px 0;
  color: #fefefe;
}
.Footer > div > div > h3 {
  padding: 20px 0;
  color: #f21515;
}
.Comentarios {
  background-color: #f2f2f2;
}
.panel {
  border: 0px;
  border-radius: 8px;
  background-color: #fff;
  padding: 15px;
  margin: 8px;
  box-shadow: 3px 3px 10px #aaa;
}

@media screen and (max-width: 720px){
  .ordem-1 { order: 1; }
  .ordem-2 { order: 2; }
}

.bloco-call {
  background-color: #222840;
  color: #fefefe;
  margin: 20px;
}

.bg-call {
  color: #fefefe;
  background-color: #222840;
  padding: 25px 0;
}

.NavBar {
  background-color: #fafafa;
}

/* PADDING */
.padding-20 {
  padding: 20px 0;
}
@media screen and (max-width: 720px){
  
}
.button-mb {
  padding: 3px 8px;
  border:1px solid #aaa;
  border-radius: 5px;
  margin: 10px;
  height: 40px;
  width: 100px;
  cursor: pointer;
}
.button-mb:hover {
  background-color: #212640;
  color: #fefefe;
}